import React from "react"
import { Link } from "gatsby"
import PropTypes from "prop-types"
import logo from "../pages/images/codeit-icon.png"

const Header = ({ siteTitle }) => (
  <header>
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
      <div className="container">
        <Link to="/">
          <div className="navbar-brand ">
            <img src={logo} height="42" alt=""></img> {siteTitle}
          </div>
        </Link>
        <span className="badge badge-pill badge-danger m-1">Beta</span>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarResponsive">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item" id="content-desktop">
              <Link to="/about">
                <span className="nav-link">
                  عنــا <i className="far fa-address-card"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item active" id="content-mobile">
              <Link to="/">
                <span className="nav-link">
                  الرئيسية <i className="fas fa-home"></i>
                </span>
              </Link>
            </li>

            <li className="nav-item" id="content-desktop">
              <Link to="/blog">
                <span className="nav-link">
                  المدونة <i className="fas fa-blog"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/category">
                <span className="nav-link">
                  الأقسام <i className="far fa-list-alt"></i>
                </span>
              </Link>
            </li>

            <li className="nav-item" id="content-desktop">
              <Link to="/contact">
                <span className="nav-link">
                  تواصل معنا <i className="fas fa-mail-bulk"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/tags">
                <span className="nav-link">
                  الوسوم <i className="fas fa-tags"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-desktop">
              <Link to="/video">
                <span className="nav-link">
                  الفيديوهات <i className="far fa-play-circle"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/video">
                <span className="nav-link">
                  الفيديوهات <i className="far fa-play-circle"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-desktop">
              <Link to="/tags">
                <span className="nav-link">
                  الوسوم <i className="fas fa-tags"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/contact">
                <span className="nav-link">
                  تواصل معنا <i className="fas fa-mail-bulk"></i>
                </span>
              </Link>
            </li>

            <li className="nav-item" id="content-desktop">
              <Link to="/category">
                <span className="nav-link">
                  الأقسام <i className="far fa-list-alt"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/blog">
                <span className="nav-link">
                  المدونة <i className="fas fa-blog"></i>
                </span>
              </Link>
            </li>

            <li className="nav-item active" id="content-desktop">
              <Link to="/">
                <span className="nav-link">
                  الرئيسية <i className="fas fa-home"></i>
                </span>
              </Link>
            </li>
            <li className="nav-item" id="content-mobile">
              <Link to="/about">
                <span className="nav-link">
                  عنــا <i className="far fa-address-card"></i>
                </span>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
