import { PropTypes } from "prop-types"
import React from "react"
import logo from "../pages/images/codeit-icon.png"

const Footer = () => (
  <footer className="py-5 bg-dark">
    <div className="container">
      <p className="m-0 text-center text-white">
        {" "}
        © {new Date().getFullYear()}, <img src={logo} height="25" alt=""></img>
        Codeit in Arabic . All Right Reserved
      </p>
    </div>
    <br />
    <div className="container text-center">
      <button className="btn btn-primary btn-circle btn-circle-sm m-1">
        <a
          href="https://www.facebook.com/codeit9"
          className="text-white stretched-link"
        >
          <i className="fab fa-facebook-f"></i>
        </a>
      </button>
      <button className="btn btn-danger btn-circle btn-circle-sm m-1">
        <a
          href="https://www.youtube.com/channel/UCxMe3TfmiMGTXREyOY3qlwg"
          className="text-white stretched-link"
        >
          <i className="fab fa-youtube"></i>
        </a>
      </button>
      <button className="btn btn-info btn-circle btn-circle-sm m-1">
        <a href="!#" className="text-white stretched-link">
          {" "}
          <i className="fab fa-twitter"></i>
        </a>
      </button>
      <button className="btn btn-warning btn-circle btn-circle-sm m-1">
        <a href="/tags" className="text-dark stretched-link">
          <i className="fa fa-tags"></i>
        </a>
      </button>
    </div>
  </footer>
)

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
}
export default Footer
